import React, { Suspense, useEffect, useRef } from "react";
import "./App.scss";
import Header from "./components/Header";
import Section from "./components/Section";
import { Canvas, useFrame } from "@react-three/fiber";
import { Html, useGLTF, OrbitControls } from "@react-three/drei";
import state from "./components/State";
import { useInView } from "react-intersection-observer";

const Model = ({ modelpath }) => {
  const gltf = useGLTF(modelpath, true);
  return <primitive object={gltf.scene} dispose={null} />;
};
const Lights = () => {
  return (
    <>
      <ambientLight intensity={0.3} />
      <directionalLight position={[10, 10, 5]} intensity={1} />
      <directionalLight position={[0, 10, 0]} intensity={1.5} />
      <spotLight intensity={1} position={[0, 1000, 0]} castShadow />
    </>
  );
};
const HTMLContent = ({
  children,
  modelPath,
  positionY,
  posX,
  posY,
  rotX,
  rotY,
  scale,
  domContent,
  bgColor,
  rotSpd,
  posZ
}) => {
  const ref = useRef()
  useFrame(() => (ref.current.rotation.y += rotSpd))

  const [refItem, inView] = useInView({
    threshold: 0,
  });

  useEffect(() => {
    inView && (document.body.style.background = bgColor);
  }, [inView]);

  return (
    <Section factor={1.5} offset={1}>
      <group position={[0, positionY, 0]}>
        <mesh
          position={[posX, posY, posZ]}
          ref={ref}
          scale={scale}
          rotation-y={rotY}
          rotation-x={rotX}
        >
          <Model modelpath={modelPath} />
        </mesh>
        <OrbitControls/>
        <Html fullscreen portal={domContent}>
          <div className="container" ref={refItem}>
            {children}
          </div>
        </Html>
      </group>
    </Section>
  );
};

function App() {
  const domContent = useRef();
  const scrollArea = useRef();
  const onScroll = (e) => (state.top.current = e.target.scrollTop);
  useEffect(() => void onScroll({ target: scrollArea.current }), []);
  return (
    <>
      <Header />
      <div className="container">
        <Canvas
          shadows
          camera={{ position: [0, 0, 360], fov: 70 }}
          gl={{ antialias: true }}
        >
          <Lights />
          <Suspense fallback={null}>
            <HTMLContent
              domContent={domContent}
              bgColor={"#f15946"}
              modelPath="/scene.gltf"
              positionY={700}
              posX={0}
              posY={-10}
              rotY={0}
              rotX={[Math.PI / 6]}
              scale={1.5}
              rotSpd={0.01}
              posZ={0}
            >
              <h1 className="title">Digitalisez votre apprentissage</h1>
            </HTMLContent>
            <HTMLContent
              domContent={domContent}
              bgColor={"#571ec1"}
              modelPath="/chalkboard.gltf"
              positionY={-50}
              posX={-4}
              posY={-15}
              rotY={[-5 * Math.PI / 5]}
              rotX={0}
              scale={2.5}
              rotSpd={0}
              posZ={0}
            >
              <h1 className="title">Depassez vos limites et vos potentiels</h1>
            </HTMLContent>
          </Suspense>
        </Canvas>
        <div className="scrollArea" ref={scrollArea} onScroll={onScroll}>
          <div style={{ position: "sticky", top: 0 }} ref={domContent}></div>
          <div
            style={{ height: `${state.sections * 65}vh`, width: "90vw" }}
          ></div>
        </div>
      </div>
    </>
  );
}

export default App;
